// Colors
//--------------------
$colors: (
  // accent: #ffc709,
  welcom-primary: #161616,
  welcom-secondary: #fff,
  // secondary: #8f8b89,
  bg-welcome:
    linear-gradient(180deg, rgba(196, 196, 196, 0) 25%, #bedbb0 92.19%),
  secondary-text: rgba(255, 255, 255, 0.3),
  accent: #bedbb0,
  accent-hover: #9dc888,
  bg-welcome-card: #151515,
  bg-overlay: rgba(21, 21, 21, 0.3),
  label-pink: #e09cb5,
  label-blue: #8fa1d0
);

@function color($colorSelect) {
  @return map_get($colors, $colorSelect);
}

// Transition
//--------------------
// $transition: 0.2s;
$transition: 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s;

// Corner radius
//--------------------
$radius: 4px;

$sm: 576px;
$md: 768px;
$lg: 992px;
$xl: 1200px;

$maxWidthContent: 1216px;
$maxWidthContentMobile: 600px;

$fonts: (
  r: 'Manrope-Regular',
  m: 'Manrope-Medium',
  b: 'Manrope-SemiBold',
  rmont: 'Montserrat-Regular',
);
//Пример использования      font-family: map-get($fonts, r), sans-serif;

$max-width-content: 1218px;

:root {
  --bgWelcome: linear-gradient(
    180deg,
    rgba(196, 196, 196, 0) 25%,
    #bedbb0 92.19%
  );
  --secondaryText: rgba(255, 255, 255, 0.3);
  --accent: #bedbb0;
  --accentHover: #9dc888;
  --bgWelcomeCard: #151515;
  --bgOverlay: rgba(21, 21, 21, 0.3);
  --labelPink: #e09cb5;
  --labelBlue: #8fa1d0;
}

// DARK THEME
