  --textThemeMenu: rgba(255, 255, 255, 0.5);
  --bgModal: #151515;
  --modalTitle: #ffffff;
  --modalBtnText: #161616;
  --iconPlus: rgba(255, 255, 255, 1);
  --colorFilters: rgba(255, 255, 255, 0.8);
  --iconsCard: rgba(255, 255, 255, 0.5);
  --bgIconPlus: #ffffff;
  --bgIconPlusCard: #ffffff;
  --bgSbTitleBoard: #1f1f1f;
  --inputBorder: rgba(190, 219, 176, 0.4);
  --inputBorderAccent: rgba(190, 219, 176, 1);
